.Navigate{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 4rem;
    background-color: rgb(0, 65, 58);
    opacity: 0.95;
    display: flex;
    align-items: center;
    justify-content: center;
}

i{
    font-size: 3rem;
    color: white;
}

.filler{
    height: 4rem;;
}

.image_pic{
    display: block;
    height: 3rem;
    margin-bottom: 0rem;
    margin-right: 0.5rem;
}

.hero{
    text-decoration: none;
    cursor: pointer;
    position: absolute;
    left: 1rem;
    display: flex;
    align-items: center;
    font-size: 1.9rem;
    color: rgb(255, 255, 255);
    font-weight: 500;
}

.hero:hover{
    color: rgb(168, 252, 243);
}

.tabOptions{
    font-size: 1.1rem;
    font-weight: 500;
    text-decoration: none;
    color: white;
    margin-left: 1rem;
    margin-right: 1rem;
}

.tabOptions:hover{
    color: rgb(51, 255, 231);
    font-weight: 500;
}

.reservation{
    cursor: pointer;
    color: white;
    position: absolute;
    right: 0rem;
    height: inherit;
    background-color: rgb(0, 88, 78);
    display: flex;
    justify-content: center;
    align-items: center;
    padding-left: 1rem;
    padding-right: 1rem;
}

.reservation:hover{
    background-color: rgb(4, 119, 106);
}


/* -----------Card------------ */
.card{
    display: flex;
    flex-direction: column;
    padding: 1rem;  
    width: 15rem;
    height: 16rem;
    border: 2px solid rgb(116, 189, 180);
}

.card_title{
    display: flex;
    font-size: 1rem;
    font-weight: 600;
    color: rgb(0, 101, 89);
}

.card_desc p{
    margin-top: 1rem;
    text-align: left;
    color: rgb(81, 81, 81);
    font-size: 1rem;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 5;  
    overflow: hidden;   /* Hide the overflow */
    text-overflow: ellipsis;
    line-height: 1.6rem;
}

/* -----------Home Page------------ */

.home{
    background: linear-gradient( rgba(0, 23, 20, 0.8), rgba(0, 23, 20, 0.8) ), url('../assets/doctors.jpg');
    background-size: cover;
    height: 100vh;
    width: 100;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.company_name{
    font-size: 1.5rem;
    font-weight: 400;
    color: white;
}

.entry_names{
    font-size: 5rem;
    font-weight: 400;
    color: white;
}

.package_contents{
    background-color: rgb(206, 225, 223);
    min-height: 80vh;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}

.package1{
    width: 100%;
    display: flex;
    justify-content: space-evenly;
}


/* -----------About page------------ */


.about_us{
    background: linear-gradient( rgba(0, 23, 20, 0.8), rgba(0, 23, 20, 0.8) ), url('../assets/microscope.jpg');
    background-size: cover;
    height: 104vh;
    width: 100;
    display: flex;
    align-items: center;
    flex-direction: column;
}

.about_us .page_title{
    margin-top: 2.5rem;
    font-size: 4.1rem;
    font-weight: 400;
    color: white;
}

.content{
    width: 70%;
}

.content p{
    color: rgb(159, 255, 244);
    font-size: 1.5rem;
    font-style: italic;
    line-height: 3rem;
}


/* -----------Doctors Page------------ */


.doctors{
    background: linear-gradient( rgba(0, 23, 20, 0.8), rgba(0, 23, 20, 0.8) ), url('../assets/kit.jpg');
    background-size: cover;
    height: 104vh;
    width: 100;
    display: flex;
    align-items: center;
    flex-direction: column;
}

.doctors .page_title{
    margin-top: .5rem;
    font-size: 4.1rem;
    font-weight: 400;
    color: white;
}